.cell {
  background: lightgray;
  border: solid 1px black;
  box-sizing: border-box;
  cursor: pointer;
  width: 24px;
  height: 24px;

  display: flex;
}

.cell--wall {
  background: black;
}

.cell--path {
  background: blue;
}

.cell--visited {
  background: yellow;
}

.cell--start {
  background: green;
}

.cell--finish {
  background: red;
}

.cell:hover {
  background: lightblue;
}
